export function getQuery (url = location.href, name) {
  const result = {}
  const arr = url.split('?')
  const queryStr = arr[arr.length - 1]
  const searchString = new URLSearchParams(queryStr).toString()

  if (searchString) {
    const queryArray = searchString.split('&')
    for (let i = 0; i < queryArray.length; i++) {
      const item = queryArray[i].split('=')
      result[item[0]] = decodeURIComponent(item[1])
    }
  }
  if (name) {
    return result[name]
  }
  return result
}

export function getMonthLastDate (year, month) {
  return new Date(year, month, 0)
}

export function getCss (o, key) {
  return o.currentStyle ? o.currentStyle[key] : document.defaultView.getComputedStyle(o, false)[key]
}

export function uuid () {
  return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, (c) =>
    (c ^ (crypto.getRandomValues(new Uint8Array(1))[0] & (15 >> (c / 4)))).toString(16)
  )
}

export function randomColor () {
  return `hsl(${Math.floor(Math.random() * 360)}, ${Math.floor(Math.random() * 100)}%, ${Math.floor(
    Math.random() * 100
  )}%)`
}

export function nextFrame (cb) {
  requestAnimationFrame(() => {
    requestAnimationFrame(cb)
  })
}
