<template>
  <div class="h-full relative invitation-card" ref="page" :style="style">
    <template v-if="isActive">
      <Position
        v-for="(item, index) in items"
        :animate="item.animate"
        :position="item.position"
        :key="index"
      >
        <VNode :node="item.render"></VNode>
      </Position>
    </template>
  </div>
</template>

<script>
import Position from '@/views/InvitationCard/components/Position'
import VNode from '@/views/VNode/VNode'
export default {
  name: 'InvitationCard',
  components: { VNode, Position },
  props: {
    detail: Object,
    isActive: Boolean
  },
  computed: {
    items () {
      return this.detail.items
    },
    style () {
      return this.detail.style
    }
  }
}
</script>

<style scoped lang="less">
.invitation-card {
  scroll-snap-align: start;
}
</style>
